/* Reset styles */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

body {
  font-family: var(--font-main);
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: 0;
  background-color: var(--color-bckg);
  color: var(--color-text);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

button {
  background-color: var(--color-btn);
  fill: var(--color-btn-fill);
  color: var(--color-btn-fill);
  border: none;
  border-radius: 30px;
}

button:hover,
button:focus {
  background-color: var(--color-btn-active);
}