*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --color-text: #010101cc;
  --color-bckg: #e8e8e2;
  --color-card-bckg: #fafafa;
  --color-img-bckg: #e8e8e2;
  --color-btn: #6d8434;
  --color-btn-fill: #e8e8e2;
  --color-btn-active: #586f1f;
  --color-btn-small: #e8e8e2;
  --color-btn-small-fill: #6d8434;
  --color-btn-small-active: #6d8434;
  --color-btn-small-fill-active: #e8e8e2;
  --color-btn-arrow: #fafafa;
  --color-btn-arrow-fill: #a3a3a3;
  --color-btn-arrow-active: #6d8434;
  --color-btn-arrow-fill-active: #fafafa;
  --color-btn-send: #e8e8e2;
  --color-btn-send-text-active: #6d8434;
  --color-btn-cancel: #01010112;
  --color-btn-cancel-active: #010101;
  --color-btn-cancel-text-active: #e8e8e2;
  --color-prop-text: #0101014d;
  --font-main: "Neue Montreal", sans-serif;
  --font-logo: "FONTSPRING DEMO - Quincy CF Black", serif;
  --indent-col: 20px;
  --indent-row: 20px;
  --items-number: 1;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  font-family: var(--font-main);
  letter-spacing: 0;
  background-color: var(--color-bckg);
  color: var(--color-text);
  font-size: 14px;
  line-height: 1.29;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

button {
  background-color: var(--color-btn);
  fill: var(--color-btn-fill);
  color: var(--color-btn-fill);
  border: none;
  border-radius: 30px;
}

button:hover, button:focus {
  background-color: var(--color-btn-active);
}

.container {
  min-width: 320px;
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

.section {
  padding: 40px 0;
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .section {
    padding: 50px 0;
  }
}

@media only screen and (min-width: 1272px) {
  .container {
    max-width: 1272px;
    padding: 0 16px;
  }

  .section {
    padding: 75px 0;
  }
}

