.popular-title {
  color: var(--color-btn-cancel-active);
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
}

.popular-list {
  flex-wrap: wrap;
  align-items: center;
  gap: 14px;
  display: flex;
}

.popular-item {
  background: var(--color-card-bckg);
  border: 1px solid #fff3;
  border-radius: 10px;
  width: 335px;
  padding: 12px;
  display: flex;
  position: relative;
}

.popular-product-img {
  background: var(--color-bckg);
  border-radius: 10px;
  flex-shrink: 0;
  justify-content: center;
  width: 74px;
  height: 74px;
  margin-right: 10px;
  display: flex;
}

.popular-item-info {
  flex-flow: column wrap;
  display: flex;
}

.popular-info-title {
  color: var(--color-btn-cancel-active);
  margin-bottom: 12px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.popular-accent {
  color: #0101014d;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
}

.popular-category {
  color: var(--color-btn-cancel-active);
  margin-bottom: 9px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
}

.popular-info-descr {
  color: var(--color-btn-cancel-active);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.17;
}

.cart-btn {
  background: var(--color-bckg);
  border-radius: 30px;
  justify-content: center;
  width: 20px;
  height: 20px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.cart-btn:hover, .cart-btn:focus {
  background-color: var(--color-btn);
}

.cart-btn-icon {
  fill: var(--color-btn);
}

.cart-btn-icon:hover, .cart-btn-icon:focus {
  fill: var(--color-bckg);
}

@media only screen and (min-width: 768px) {
  .popular-title {
    font-size: 28px;
  }

  .popular-item {
    width: 275px;
  }

  .popular-item.is-hidden {
    display: none;
  }
}

@media only screen and (min-width: 1272px) {
  .popular-item.is-not-hidden {
    display: flex;
  }
}

