#products-list.section {
  padding-top: 40px;
}

#products-list .products-list {
  column-gap: var(--indent-col);
  row-gap: var(--indent-row);
  flex-wrap: wrap;
  width: 100%;
  display: flex;
}

#products-list .product-item {
  background-color: var(--color-card-bckg);
  flex-basis: calc((100% - (var(--items-number)  - 1) * var(--indent-col)) / var(--items-number));
  border-radius: 20px;
  padding: 20px;
  position: relative;
}

.product-img-container {
  background-color: var(--color-img-bckg);
  border-radius: 10px;
  justify-content: center;
  padding: 19px 0;
  display: flex;
}

#products-list .product-name {
  color: var(--color-text);
  margin-top: 20px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

#products-list .product-props {
  gap: 9px;
  margin-top: 10px;
  display: flex;
}

#products-list .prop {
  gap: 4px;
  font-size: 12px;
  line-height: 1.17;
  display: flex;
}

#products-list .prop-name {
  color: var(--color-prop-text);
}

#products-list .product-buy-container {
  justify-content: space-between;
  margin-top: 40px;
  display: flex;
}

#products-list .price {
  align-self: flex-end;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

#products-list .btn-buy {
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  display: flex;
}

#products-list .product-discount {
  position: absolute;
  top: 3px;
  right: 10px;
}

#products-list .nothing-found {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

#products-list .nothing-found-img {
  margin-top: 20px;
}

#products-list .nothing-found-message {
  font-size: 32px;
  font-style: normal;
  font-weight: 500;
  line-height: 1;
}

@media only screen and (min-width: 768px) {
  #products-list .products-list {
    --indent-col: 32px;
    --indent-row: 32px;
  }

  #products-list .product-item {
    --items-number: 2;
  }

  #products-list .product-discount {
    top: -3px;
  }
}

@media only screen and (min-width: 1272px) {
  #products-list .products-list {
    --indent-col: 20px;
    --indent-row: 40px;
    width: 925px;
  }

  #products-list .product-item {
    --items-number: 3;
  }

  #products-list .product-props {
    flex-wrap: wrap;
  }
}

.desktop, .tablet {
  display: none;
}

@media only screen and (min-width: 768px) {
  .tablet {
    display: list-item;
  }
}

@media only screen and (min-width: 1272px) {
  .desktop {
    display: list-item;
  }
}



