.backdrop > .container {
  min-width: 280px;
  max-width: 335px;
}

.backdrop {
  background-color: #01010166;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  background-color: var(--color-card-bckg);
  border-radius: 20px;
  width: calc(100% - 40px);
  padding: 40px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-close-btn {
  fill: #010101;
  background: none;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.cart-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.modal-cart-btn {
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  font-weight: 500;
  line-height: 1.28;
  display: flex;
}

.modal-img {
  background-color: var(--color-img-bckg);
  border-radius: 10px;
  width: 295px;
  height: 212px;
  margin: 0 auto 20px;
}

.modal-product-name {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.product-info {
  flex-direction: column;
  gap: 10px;
  margin-bottom: 40px;
  display: flex;
